import React from 'react'
import Header from '../../components/header/Header'
import Footer from '../../components/footer/Footer'
import { useContext, useState, useEffect } from 'react'
import './shoppingCart.styles.css'
import { DishData } from '../../App'

function ShoppingCart() {

    const {name, price, link} = useContext(DishData);
    const [cart, setCart] = useState([]);
    console.log(cart)
    useEffect(() => {
        setCart( [...cart, { name, price, link }])
    }, [])


    useEffect(() => {
        console.log(cart)
    }, [cart])

    return (
        
        <>
            <Header/>
                <div className="cart__title">Your Cart</div>
                <div className="item-container">

                    { cart ? cart.map(item => (
                        <div className="item">
                            <h4>Items in cart: {cart.length}</h4>
                            <img src={item.link} alt="Img" className="item-thumb" />
                            <div className="item-name">{item.name}</div>
                            <input type="number" className="item-quantity" />
                            <div className="item-total-price">{item.price}</div>
                        </div>
                    )) : (<h2>Empty</h2>) }
                 
                
                </div>
            <Footer/>
        </>
        
    )
}

export default ShoppingCart




